export default {
	onLoad() {
		uni.getStorage({
			key: 'openId',
			success: res => {
				this.openId = res.data;
			}
		});
	},
	data() {
		return {
			imgUrl1: '',
			imgUrl2: '',
			imgUrl3: '',
			imgUrl4: '',
			imgUrl5: '',
			imgUrl6: '',
			openId: ''
		};
	},
	methods: {
		onSubmit() {
			if (this.imgUrl1 == '' || this.imgUrl2 == '') {
				uni.showToast({
					title: '请上传身份证照片',
					icon: 'none'
				});
			} else if (this.imgUrl3 == '') {
				uni.showToast({
					title: '请上传营业执照',
					icon: 'none'
				});
			} else {
				uni.showToast({
					title: '提交成功'
				});
				setTimeout(() => {
					uni.navigateTo({
						url: 'verify-result'
					});
				}, 1000);
			}
		},
		onChooseImg1() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				success: res => {
					this.imgUrl1 = res.tempFilePaths[0];
					uni.uploadFile({
						url: 'https://www.finetwm.com/xmRepair/shopInfo/upload', //仅为示例，非真实的接口地址
						filePath: this.imgUrl1,
						header: {
							'content-Type': 'multipart/form-data'
						},
						name: 'file',
						formData: {
							type: 1,
							openid: this.openId
						}
					});
				}
			});
		},
		onChooseImg2() {
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				success: res => {
					this.imgUrl2 = res.tempFilePaths[0];
